import { Button, Space } from "antd";
import { useTranslation } from "react-i18next";

import classes from "./Apartments.module.scss";
import useApartmentsColumns from "./ApartmentsColumns";
import { Select, Table } from "components/shared/AntComponents";
import { useState } from "react";
import { ContractModal, HouseSchema } from "./components";

type Props = {};

const data = Array(10).fill({
	id: 1,
	a: "Олой сити",
	b: 1,
	c: 2,
	d: 3,
	e: 4
});

const Apartments = (props: Props) => {
	const { t } = useTranslation();
	const columns = useApartmentsColumns();
	const [contract, setContract] = useState(false);
	const [receipt, setReceipt] = useState(false);

	return (
		<div className={classes.main}>
			<HouseSchema />
			<Table
				rowKey={"id"}
				loading={false}
				columns={columns}
				dataSource={data}
				headerLeftFilter={
					<Space size={20}>
						<Select size="large" width={200} options={[]} placeholder={t("Мажмуа номи")} />
						<Select size="large" width={140} options={[]} placeholder={t("Бино рақами")} />
						<Select size="large" width={140} options={[]} placeholder={t("Қават")} />
					</Space>
				}
				headerRightFilter={
					<Space size={20}>
						<Button onClick={() => setContract(true)} size="large" style={{ borderRadius: 50 }} type="default">
							{t("Шартнома")}
						</Button>

						<Button onClick={() => setReceipt(true)} size="large" style={{ borderRadius: 50 }} type="primary">
							{t("Кветанция")}
						</Button>
					</Space>
				}
			/>

			{receipt && <ContractModal toggle={receipt} onCancel={() => setReceipt(false)} />}
			{contract && <ContractModal toggle={contract} onCancel={() => setContract(false)} />}
		</div>
	);
};

export default Apartments;
