import { FC } from "react";
import { useTranslation } from "react-i18next";
import classes from "./HouseSchema.module.scss";
import { Button, Col, Row, Select, Space, Table, TableColumnsType } from "antd";
import { APT_STATUS_COLORS } from "constants/ui";
import { StatusDots, StatusDotsProps } from "components/shared";

const DUMMY_DATA = new Array(7)
	.fill({})
	.map((_, idx) => ({ id: String(idx + 1 * Math.random()).slice(-2) }));

type TableRecordDto = any;

const HouseSchema: FC = () => {
	const { t } = useTranslation();

	const dataColumns = DUMMY_DATA.map(({ id }, idx) => {
		return {
			title: (
				<div className="f-center">
					<div className={classes.numLabel}>
						<span>{idx + 1}</span> <p>{t("ПОДЪЕЗД")}</p>
					</div>
				</div>
			),
			align: "center",
			render() {
				return (
					<Space size="middle">
						<Button
							shape="circle"
							size="middle"
							className={classes.circleApt}
							style={{
								background:
									Math.trunc(+id * Math.random()) > 60
										? APT_STATUS_COLORS.BOOKED
										: APT_STATUS_COLORS.PAID
							}}
						>
							{Math.trunc(+id * Math.random())}
						</Button>
						<Button
							shape="circle"
							size="middle"
							className={classes.circleApt}
							style={{
								background:
									Math.trunc(+id * Math.random()) > 60
										? APT_STATUS_COLORS.PAID
										: APT_STATUS_COLORS.NOT_BOOKED
							}}
						>
							{Math.trunc(+id * Math.random())}
						</Button>
						<Button
							shape="circle"
							size="middle"
							className={classes.circleApt}
							style={{
								background:
									Math.trunc(+id * Math.random()) > 60
										? APT_STATUS_COLORS.NOT_BOOKED
										: APT_STATUS_COLORS.BOOKED
							}}
						>
							{Math.trunc(+id * Math.random())}
						</Button>
					</Space>
				);
			}
		};
	}) as TableColumnsType<TableRecordDto>;

	const columns: TableColumnsType<TableRecordDto> = [
		{
			render: (_, __, idx) => (
				<div className="f-center">
					<div className={classes.numLabel}>
						<span>{7 - idx}</span>
						<p>{t("Қават")}</p>
					</div>
				</div>
			),
			align: "center"
		},
		...dataColumns
	];

	const statusItems: StatusDotsProps["items"] = [
		{
			title: t("Банд қилинган"),
			color: APT_STATUS_COLORS.BOOKED
		},
		{
			title: t("Банд қилинмаган"),
			color: APT_STATUS_COLORS.NOT_BOOKED
		},
		{
			title: t("Тўлов қилинган"),
			color: APT_STATUS_COLORS.PAID
		}
	];

	return (
		<div className={classes.wrapper}>
			<Row justify="space-between" align="middle" gutter={[30, 30]} className="mb-30">
				<Col xs={24} md={12}>
					<Space size="large">
						<Select size="large" placeholder={t("Олой сити")} />
						<Select size="large" placeholder={t("Бино рақами")} />
					</Space>
				</Col>
				<Col>
					<StatusDots items={statusItems} />
				</Col>
			</Row>

			<Table
				size="small"
				columns={columns}
				dataSource={new Array(7).fill({})}
				pagination={false}
				scroll={{ x: 1600 }}
			/>
		</div>
	);
};

export default HouseSchema;
